#contact 
 {
	background: linear-gradient(-45deg, #0f7dfa, #085fc2, #034896, #05a1f5);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	box-shadow: 0 25px 20px -20px rgba(0, 0, 0, 0.4), 0 -25px 20px -20px rgba(0, 0, 0, 0.4);	;
 }

 @keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.contact h2 {
	font-size: 2.5em;
	text-align: center;
	margin: 0;
}

.contact {
	background-color: white;
	border-radius: 10px;
	width: 50rem;
	margin: 0 auto;
	padding: 40px 0;
	display: flex; 
	flex-direction: column;
	justify-content: center; 
	align-items: center;
}
  
.form {
	display: flex; 
	flex-direction: column;
	width: 400px;
}
  
input[type=text], input[type=email], textarea {
	width: 100%;
	padding: 12px;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-sizing: border-box;
	margin-top: 6px;
	margin-bottom: 16px;
	resize: vertical;
}
  
input[type=submit] {
	background-color: #969696;
	color: white;
	padding: 12px 20px;
	border: none;
	border-radius: 4px;
	cursor: pointer;
}
  
input[type=submit]:hover {
	background-color: #424242;
}