#particles {
	position: absolute;
	z-index: -100;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

#home {
	position: relative;
	top: 0;
	/* background-color: #f0f0f0; */
	/* background-image: url("https://images.pond5.com/abstract-blue-geometric-background-moving-footage-076294250_prevstill.jpeg");  */
	width: 100%;
	height: 70vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
	box-shadow: 0 25px 20px -20px rgba(0, 0, 0, 0.4);
	font-weight: 300;
	color: #f0f0f0;
}
  
#home p {
	font-size: 20px;
}

#home h1, #home p {
	text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
  }
  

.white::before
{
	background-image: linear-gradient(to right, transparent, rgb(255, 255, 255), transparent);
}